$component: --mt-quadro-04;

:host {
  #{$component}-border: 2px dotted var(--mt-color-quadros-linha-01);

  #{$component}-left-margin: 0 6px 0 0;

  #{$component}-inner-container-padding: 12px;

  #{$component}-text-color: var(--mt-color-quadros-fonte-02);
  #{$component}-text-font-family: 'Noto Sans', sans-serif;
  #{$component}-text-font-weight: 500;
  #{$component}-text-text-transform: none;
  #{$component}-text-font-size: 16px;
  #{$component}-text-background-color: var(--mt-color-quadros-bg-02);

  display: block;
}

.mt-quadro-04 {
  &__container {
    background-color: var(#{$component}-background-color);
    display: flex;
    align-items: center;
    border: var(#{$component}-border);
  }

  &__left {
    margin: var(#{$component}-left-margin);
  }

  &__inner-container {
    padding: var(#{$component}-inner-container-padding);
  }

  &__text {
    color: var(#{$component}-text-color);
    font-family: var(#{$component}-text-font-family);
    font-weight: var(#{$component}-text-font-weight);
    text-transform: var(#{$component}-text-text-transform);
    font-size: var(#{$component}-text-font-size);
    line-height: 1;
    margin: 0;
  }
}
